import React from "react"
import Layout from "../components/layout"
import { graphql } from "gatsby"
import NewsletterBox from "../components/newsletterBox"
import SEO from "../components/seo"
import Button from "react-bulma-components/lib/components/button"
import Container from "react-bulma-components/lib/components/container"
import Section from "react-bulma-components/lib/components/section"
import NewsletterSubscribe from "../components/newsletterSubscribe";
import Navbar from "../components/navbar";

export default ({ data }) => {
  console.log(data)
  return (
    <Layout>
      <SEO title="Archiv" keywords={[`ecotainment`, `newsletter`, `archiv`, data.allNewJson.edges[0].node.title]} />
      <Container>
        <Navbar/>
        <NewsletterSubscribe />
        {data.allNewJson.edges.map(({ node }, index) => (
          <Section>
            <NewsletterBox node={node} />
            <Button
              onClick={() => {
                typeof window.history !== 'undefined' && window.history.back()
              }}
              color={'primary'}
              fullwidth={true}
            >
              Zur√ºck
            </Button>
          </Section>
        ))}
      </Container>
    </Layout>
  )
}

export const query = graphql`
    query($slug: String!) {
        allNewJson(filter: { slug: { eq: $slug } }) {
            edges {
                node {
                    published
                    title
                    tags
                    newsletter {
                        welcome
                        intro
                        parts {
                            emoji {
                                label
                                icon
                            }
                            title
                            tags
                            text
                        }
                        outro
                    }
                }
            }
        }
    }
`
